import React, { useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import styles from './Main.module.scss';
import { randomIntFromInterval } from '../../utils/numberUtils';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Stack from '@mui/material/Stack';
import Timer from '../Timer/Timer';
import DisplayNumber from '../DisplayNumber/DisplayNumber';

const ranges = [10, 100, 1000, 10000];

const Main = () => {
  const [generatedNumber, setGeneratedNumber] = useState(0);
  const [range, setRange] = useState(10);
  const [started, setStarted] = useState(false);
  const [numbers, setNumbers] = useState([]);
  const [isActive, setIsActive] = useState(false);
  const [isPaused, setIsPaused] = useState(true);
  const [time, setTime] = useState(0);

  useEffect(() => {
    let interval = null;

    if (isActive && isPaused === false) {
      interval = setInterval(() => {
        setTime((time) => time + 10);
      }, 10);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, isPaused]);

  const handleStart = () => {
    setIsActive(true);
    setIsPaused(false);
  };

  const handlePauseResume = () => {
    setIsPaused(!isPaused);
  };

  const handleReset = () => {
    setIsActive(false);
    setTime(0);
  };

  const handleStop = () => {
    setStarted(false);
    setGeneratedNumber(null);
    setNumbers([]);
    handleReset();
  }

  const handleClick = () => {
    // the last one
    if (started && numbers[numbers.length - 1] === generatedNumber) {
      setStarted(false);
      setGeneratedNumber(null);
      setNumbers([]);
      handlePauseResume();
    }

    // next step
    if (started) {
      console.log({ n: generatedNumber + 1 });
      setGeneratedNumber(generatedNumber + 1);
    }

    // start
    if (!started) {
      handleReset();
      const arr = Array.from({ length: 10 }, () => randomIntFromInterval(1, range));
      setNumbers(arr);
      setGeneratedNumber(0)
      setStarted(true);
      handleStart();
    }
  }

  const handleRange = (_, newValue) => setRange(newValue);

  const getButtonName = () => {
    if (started && numbers[numbers.length - 1] === generatedNumber) {
      return 'Finish';
    }

    if (started) {
      return 'Next number';
    }

    return 'Start';
  };

  return (
    <div className={styles.main}>
      <div className={styles.container}>
        <h2>
          Memorize the numbers
        </h2>
        <Stack direction="row" spacing={4} className={styles.range}>
          <h4>Max:</h4>
          <ToggleButtonGroup
            value={range}
            exclusive
            onChange={handleRange}
            disabled={started}
          >
            {ranges.map(range => (
              <ToggleButton value={range} key={range}>
                {range}
              </ToggleButton>
            ))}
          </ToggleButtonGroup>
        </Stack>
        <Stack direction="row" spacing={4} className={styles.range}>
          <Button
            variant="contained"
            onClick={handleClick}
          >
            {getButtonName()}
          </Button>
          {started && (
            <Button
              variant="contained"
              onClick={handleStop}
            >
              Stop
            </Button>
          )}
        </Stack>
        {generatedNumber !== null && (<DisplayNumber generatedNumber={numbers[generatedNumber]}/>)}
        <Timer started={started} time={time}/>
      </div>
    </div>
  );
};

export default Main;
