import React from 'react';
import Tooltip from '@mui/material/Tooltip';
import { convertToString } from '../../utils/numberUtils';
import ClickAwayListener from '@mui/material/ClickAwayListener';

import styles from './DisplayNumber.module.scss';

type Props = {
  generatedNumber: number;
}

const DisplayNumber = ({ generatedNumber }: Props) => {
  const [open, setOpen] = React.useState(false);

  const handleTooltipClose = () => {
    setOpen(false);
  };

  const handleTooltipOpen = () => {
    setOpen(true);
  };

  return (
    <>
      <ClickAwayListener onClickAway={handleTooltipClose}>
        <Tooltip
          PopperProps={{
            disablePortal: true,
          }}
          onClose={handleTooltipClose}
          open={open}
          disableFocusListener
          disableHoverListener
          disableTouchListener
          title={convertToString(generatedNumber)}
        >
          <h1 className={styles.number} onClick={handleTooltipOpen}>{generatedNumber}</h1>
        </Tooltip>
      </ClickAwayListener>
    </>
  );
};

export default DisplayNumber;
